import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { User } from '../auth/user.model';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class UsersService {
  constructor(private http: HttpClient) {}

  users = new BehaviorSubject<[User]>(null);

  getAll() {
    return this.http.get(baseUrl + '/user').subscribe((res) => {
      this.users.next(res['data']);
    });
  }

  addShipper(userId) {
    return this.http.post(baseUrl + `/shipper/addShipper/${userId}`, {});
  }

  deleteShipper(userId) {
    return this.http.delete(baseUrl + `/shipper/deleteShipper/${userId}`);
  }

  addLeader(userId) {
    return this.http.post(baseUrl + `/leader/addLeader/${userId}`, {});
  }

  deleteLeader(userId) {
    return this.http.delete(baseUrl + `/leader/deleteLeader/${userId}`);
  }

  addOwner(userId) {
    return this.http.post(baseUrl + `/owner/addOwner/${userId}`, {});
  }

  deleteOwner(userId) {
    return this.http.delete(baseUrl + `/owner/deleteOwner/${userId}`);
  }

  lockOne(itemId) {
    return this.http.get(baseUrl + `/user/lock/${itemId}`);
  }

  unlockOne(itemId) {
    return this.http.get(baseUrl + `/user/unlock/${itemId}`);
  }
}
